import React, { useState, useEffect } from 'react';
import GameCanvas from './GameCanvas';
import { GameConfig } from './GameSelector';
import { GameTemplateFactory, GameType, TemplateInfo } from '../game-engine/GameTemplateFactory';

interface TemplateTestModeProps {
  onExit: () => void;
}

const TemplateTestMode: React.FC<TemplateTestModeProps> = ({ onExit }) => {
  const [selectedTemplate, setSelectedTemplate] = useState<GameType | null>(null);
  const [testResult, setTestResult] = useState<string>('');
  const [gameKey, setGameKey] = useState(0);
  const [implementationStatus, setImplementationStatus] = useState<Record<GameType, any>>({});
  const [isLoading, setIsLoading] = useState(true);
  const [availableTemplates, setAvailableTemplates] = useState<TemplateInfo[]>([]);

  // 
  useEffect(() => {
    const checkImplementations = async () => {
      setIsLoading(true);
      try {
        console.log('...');
        
        // 
        const templates = await GameTemplateFactory.getAllTemplates();
        setAvailableTemplates(templates);
        
        console.log(`${templates.length}`);
        
        // 
        const statuses = await GameTemplateFactory.checkAllImplementations();
        setImplementationStatus(statuses);
        
        console.log(':', statuses);
      } catch (error) {
        console.error(':', error);
        setAvailableTemplates([]);
        setImplementationStatus({});
      } finally {
        setIsLoading(false);
      }
    };

    checkImplementations();
  }, []);

  // 
  const createTestConfig = async (templateId: GameType): Promise<GameConfig | null> => {
    try {
      const templateInfo = await GameTemplateFactory.getTemplateInfo(templateId);
      
      if (!templateInfo) {
        throw new Error(`: ${templateId}`);
      }

      return {
        gameType: templateInfo.id as any,
        characterType: 'girl',
        difficulty: templateInfo.defaultSettings.difficulty,
        duration: templateInfo.defaultSettings.duration,
        targetScore: templateInfo.defaultSettings.targetScore
      };
    } catch (error) {
      console.error(':', error);
      return null;
    }
  };

  // 
  const handleTestTemplate = async (templateId: GameType) => {
    setIsLoading(true);
    
    try {
      const status = implementationStatus[templateId];
      
      if (!status?.implemented) {
        setTestResult(` ${templateId}  CuteTapGame ...`);
      } else {
        setTestResult('');
      }
      
      const config = await createTestConfig(templateId);
      
      if (!config) {
        setTestResult(` ${templateId} `);
        setIsLoading(false);
        return;
      }
      
      setSelectedTemplate(templateId);
      setGameKey(prev => prev + 1);
    } catch (error) {
      console.error(':', error);
      setTestResult(` : ${error instanceof Error ? error.message : 'Unknown error'}`);
    } finally {
      setIsLoading(false);
    }
  };

  // 
  const handleTestResult = (success?: boolean, score?: number) => {
    const result = success ? 
      ` ! : ${score}` : 
      `  : ${score}`;
    
    const currentStatus = implementationStatus[selectedTemplate!];
    const statusInfo = currentStatus?.implemented ? 
      '()' : '(CuteTap)';
    
    setTestResult(`${result} ${statusInfo}`);
    
    setTimeout(() => {
      setSelectedTemplate(null);
      setTestResult('');
    }, 3000);
  };

  if (isLoading && availableTemplates.length === 0) {
    return (
      <div style={{ 
        padding: '40px', 
        textAlign: 'center',
        fontFamily: 'Inter, sans-serif'
      }}>
        <div style={{ fontSize: '48px', marginBottom: '20px' }}></div>
        <div style={{ fontSize: '18px', color: '#374151', marginBottom: '10px' }}>
          ...
        </div>
        <div style={{ fontSize: '14px', color: '#6b7280' }}>
          
        </div>
      </div>
    );
  }

  if (selectedTemplate) {
    return (
      <div style={{ position: 'relative' }}>
        {/*  */}
        <div style={{
          position: 'absolute',
          top: '-60px',
          left: '0',
          right: '0',
          backgroundColor: '#f3f4f6',
          borderRadius: '8px',
          padding: '10px',
          fontSize: '12px',
          textAlign: 'center',
          zIndex: 10,
          border: '2px solid #d946ef'
        }}>
          <div style={{ color: '#d946ef', fontWeight: 'bold', marginBottom: '5px' }}>
             : {selectedTemplate}
          </div>
          <div style={{ color: '#374151' }}>
            ...
          </div>
          {testResult && (
            <div style={{ 
              marginTop: '5px', 
              fontWeight: 'bold',
              color: testResult.includes('') ? '#10b981' : '#ef4444'
            }}>
              {testResult}
            </div>
          )}
        </div>

        {/*  */}
        <button
          onClick={onExit}
          style={{
            position: 'absolute',
            top: '10px',
            right: '10px',
            background: '#ef4444',
            color: 'white',
            border: 'none',
            borderRadius: '15px',
            padding: '5px 10px',
            fontSize: '12px',
            cursor: 'pointer',
            zIndex: 10
          }}
        >
          
        </button>

        {/*  */}
        <div style={{ border: '3px solid #d946ef', borderRadius: '12px' }}>
          <TestConfigProvider templateId={selectedTemplate} onGameEnd={handleTestResult} />
        </div>
      </div>
    );
  }

  return (
    <div style={{ padding: '20px', fontFamily: 'Inter, sans-serif' }}>
      <div style={{ 
        textAlign: 'center', 
        marginBottom: '20px',
        padding: '15px',
        backgroundColor: '#f3f4f6',
        borderRadius: '12px'
      }}>
        <h2 style={{ color: '#d946ef', margin: '0 0 10px 0' }}>
           
        </h2>
        <p style={{ color: '#6b7280', margin: 0, fontSize: '14px' }}>
          
        </p>
      </div>

      <button
        onClick={onExit}
        style={{
          position: 'absolute',
          top: '20px',
          right: '20px',
          background: '#6b7280',
          color: 'white',
          border: 'none',
          borderRadius: '20px',
          padding: '8px 16px',
          fontSize: '14px',
          cursor: 'pointer'
        }}
      >
        
      </button>

      {availableTemplates.length === 0 ? (
        <div style={{ textAlign: 'center', padding: '40px' }}>
          <div style={{ fontSize: '48px', marginBottom: '20px' }}></div>
          <div style={{ fontSize: '18px', color: '#dc2626' }}>
            
          </div>
          <div style={{ fontSize: '14px', color: '#6b7280', marginTop: '10px' }}>
            GameTemplateFactory 
          </div>
        </div>
      ) : (
        <div style={{ 
          display: 'grid', 
          gap: '10px',
          maxHeight: '500px',
          overflowY: 'auto'
        }}>
          {availableTemplates.map((template) => {
            // 
            const status = implementationStatus[template.id] || {
              implemented: false,
              hasFile: false, 
              isRegistered: false,
              error: ''
            };
            
            // 
            let backgroundColor, borderColor, textColor, buttonText, buttonColor, statusIcon, statusText;
            
            if (status.implemented) {
              // 
              backgroundColor = '#f0f9ff';
              borderColor = '#0ea5e9'; 
              textColor = '#0284c7';
              buttonText = ' ';
              buttonColor = '#10b981';
              statusIcon = '';
              statusText = '';
            } else if (status.hasFile && !status.isRegistered) {
              // Factory
              backgroundColor = '#f0fdf4';
              borderColor = '#22c55e';
              textColor = '#15803d'; 
              buttonText = ' Factory';
              buttonColor = '#22c55e';
              statusIcon = '';
              statusText = '';
            } else if (status.error) {
              // 
              backgroundColor = '#fef2f2';
              borderColor = '#ef4444';
              textColor = '#dc2626';
              buttonText = ' ';
              buttonColor = '#ef4444';
              statusIcon = '';
              statusText = '';
            } else {
              // 
              backgroundColor = '#fef3c7';
              borderColor = '#f59e0b';
              textColor = '#d97706';
              buttonText = ' ';
              buttonColor = '#f59e0b';
              statusIcon = '';
              statusText = 'ChatGPT';
            }
            
            return (
              <div
                key={template.id}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                  padding: '15px',
                  backgroundColor,
                  borderRadius: '10px',
                  border: `2px solid ${borderColor}`
                }}
              >
                <div style={{ flex: 1 }}>
                  <div style={{ 
                    fontWeight: 'bold', 
                    color: textColor,
                    marginBottom: '5px',
                    display: 'flex',
                    alignItems: 'center',
                    gap: '8px'
                  }}>
                    <span>{statusIcon}</span>
                    {template.name} 
                    <span style={{ fontSize: '12px' }}>
                      [{template.category}]
                    </span>
                  </div>
                  <div style={{ 
                    fontSize: '12px', 
                    color: '#6b7280',
                    marginBottom: '5px'
                  }}>
                    {template.description}
                  </div>
                  <div style={{ 
                    fontSize: '11px', 
                    color: '#9ca3af'
                  }}>
                    {template.defaultSettings.duration} | 
                    :{template.defaultSettings.targetScore} | 
                    {template.defaultSettings.difficulty}
                    <span style={{ 
                      marginLeft: '10px',
                      color: textColor,
                      fontWeight: 'bold'
                    }}>
                      | {statusText}
                    </span>
                    {status.error && (
                      <span style={{
                        marginLeft: '5px',
                        fontSize: '10px',
                        backgroundColor: '#fee2e2',
                        color: '#dc2626',
                        padding: '1px 4px',
                        borderRadius: '4px'
                      }}>
                        {status.error}
                      </span>
                    )}
                  </div>
                </div>

                <div style={{ display: 'flex', flexDirection: 'column', gap: '5px' }}>
                  <button
                    onClick={() => handleTestTemplate(template.id)}
                    disabled={isLoading}
                    style={{
                      backgroundColor: buttonColor,
                      color: 'white',
                      border: 'none',
                      borderRadius: '8px',
                      padding: '8px 16px',
                      fontSize: '14px',
                      cursor: isLoading ? 'not-allowed' : 'pointer',
                      fontWeight: 'bold',
                      minWidth: '140px',
                      opacity: isLoading ? 0.6 : 1
                    }}
                  >
                    {isLoading ? ' ...' : buttonText}
                  </button>
                </div>
              </div>
            );
          })}
        </div>
      )}

      <div style={{
        marginTop: '20px',
        padding: '15px',
        backgroundColor: '#fef3c7',
        borderRadius: '10px',
        fontSize: '12px',
        color: '#92400e'
      }}>
        <div style={{ fontWeight: 'bold', marginBottom: '5px' }}>
           
        </div>
        <div style={{ lineHeight: '1.4' }}>
          1.  <br/>
          2. <br/>  
          3. UI<br/>
          4. <br/>
          5. <br/>
          6. 
        </div>
      </div>
    </div>
  );
};

// 
interface TestConfigProviderProps {
  templateId: GameType;
  onGameEnd: (success?: boolean, score?: number) => void;
}

const TestConfigProvider: React.FC<TestConfigProviderProps> = ({ templateId, onGameEnd }) => {
  const [config, setConfig] = useState<GameConfig | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const loadConfig = async () => {
      try {
        const templateInfo = await GameTemplateFactory.getTemplateInfo(templateId);
        
        if (templateInfo) {
          setConfig({
            gameType: templateInfo.id as any,
            characterType: 'girl',
            difficulty: templateInfo.defaultSettings.difficulty,
            duration: templateInfo.defaultSettings.duration,
            targetScore: templateInfo.defaultSettings.targetScore
          });
        }
      } catch (error) {
        console.error('Config loading error:', error);
      } finally {
        setLoading(false);
      }
    };

    loadConfig();
  }, [templateId]);

  if (loading || !config) {
    return (
      <div style={{ 
        padding: '40px', 
        textAlign: 'center',
        fontSize: '16px',
        color: '#6b7280'
      }}>
        ...
      </div>
    );
  }

  return <GameCanvas config={config} onGameEnd={onGameEnd} />;
};

export default TemplateTestMode;