import * as PIXI from 'pixi.js';
import { GameTemplate, GameSettings } from './GameTemplate';
import { CuteTapGame, CuteTapSettings } from './CuteTapGame';

// 1000
export type GameType = string;

// 20
export type BaseGameType = 
  | 'cute_tap'
  | 'memory_match'
  | 'quick_dodge' 
  | 'timing_perfect'
  | 'collect_items'
  | 'jump_adventure'
  | 'friendly_shoot'
  | 'animal_chase'
  | 'rainbow_match'
  | 'puzzle_princess'
  | 'speed_friend'
  | 'spot_difference'
  | 'opposite_action'
  | 'count_star'
  | 'number_hunt'
  | 'order_master'
  | 'size_perfect'
  | 'dreamy_jump'
  | 'magical_collect'
  | 'balance_game';

// 
export type CharacterType = 'girl' | 'animal' | 'child';

// 
export type DifficultyType = 'easy' | 'normal' | 'hard';

// 
export interface UnifiedGameSettings extends GameSettings {
  gameType: GameType;
  characterType: CharacterType;
  difficulty: DifficultyType;
  duration: number;
  targetScore: number;
  customSettings?: Record<string, any>;
}

// 
export interface TemplateInfo {
  id: GameType;
  name: string;
  description: string;
  instruction: string;
  category: 'action' | 'puzzle' | 'timing' | 'reaction';
  defaultSettings: {
    duration: number;
    targetScore: number;
    difficulty: DifficultyType;
  };
  implementationStatus: 'implemented' | 'fallback' | 'missing';
}

// 
interface TemplateRegistration {
  info: TemplateInfo;
  createInstance: (app: PIXI.Application, settings: UnifiedGameSettings) => Promise<GameTemplate | null>;
}

// JSON/
interface TemplateConfigData {
  id: string;
  name: string;
  description: string;
  instruction: string;
  category: 'action' | 'puzzle' | 'timing' | 'reaction';
  duration: number;
  targetScore: number;
  difficulty?: DifficultyType;
}

// CuteTapSettings
interface ExtendedCuteTapSettings extends CuteTapSettings {
  displayName?: string;
  displayInstruction?: string;
}

